___CSS_LOADER_EXPORT___.push([module.id, `/* Mobile First Styles */
.about-section {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 55px 20px 20px 20px;
    background-color: #f0f0f0;
    position: relative;
    max-width: -webkit-fit-content;
    max-width: fit-content;
  }
  

  /* Image with rounded corners */
  .about-image {
    width: 100%;
    max-width: 500px;  /* Adjust size as needed */
    border-radius: 10px;
    display: block;
    box-shadow: 

    10px 0 15px rgba(0, 0, 0, 0.1),   /* right shadow */
    -10px 0 15px rgba(0, 0, 0, 0.1);  /* left shadow */
  }
  
  .about-us-content-title {
    margin:0;
    position:relative;
    text-align:center;
    color: rgb(35, 53, 51);
    font-family:Arial,sans-serif;
    display:flex;
    flex-direction:row;
    align-items:center;

  }

  .about-us-name{
    font-size:1rem;
    font-weight:bolder;
    letter-spacing:1.5px;
    margin: 20px 5px;
    z-index: 3;
   }
  .about-us-line {
    width:30px;
    height:3px;
    background-color:rgba(87, 128, 124,0.75);
    margin:20px 5px 20px 5px;
  }
  .about-us-company-name {
    font-size: 38px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bolder;
    color:rgb(35, 53, 51);
    line-height: 40px;
    margin: 5px 2px 15px 2px;
    padding: 5px 0px 5px 0px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);

  }
  .about-us-paragraph {
    font-size: 1.1rem;
    font-family: sans-serif;
    color: black;
    margin: 5px 2px 5px 2px;
    padding: 5px 0px 5px 0px;
    letter-spacing: 1px;
    line-height: 30px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);

    font-weight: lighter;
  }
  .about-us-button {
    display: inline-block;
    padding: 12px 24px;
    margin-top: 25px;
    font-size: 1rem;
    font-family: Arial, sans-serif;
    font-weight: 600;
    color: rgb(35, 53, 51);
    background-color: transparent;
    border: 2px solid rgb(35, 53, 51);
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
    text-decoration: none; /* For anchor tags if you use <a> instead of <button> */
    transition: background-color 0.3s ease, color 0.3s ease;
    line-height: 25px;
  }
  
  .about-us-button:hover {
    background-color: #354D4B;
    color: #fff;
  }
  
  /* Tablet (481px to 768px) */
  @media (min-width: 481px) and (max-width: 768px) {
    .about-section {
      padding: 30px;
    }
    
    .about-image {
      max-width: 350px;
    }
  }
  
  /* Desktop (769px and up) */
  @media (min-width: 769px) {
    .about-section {
      padding: 50px;
    }
    
    .about-image {
      max-width: 400px;
    }
  }
